@import "~susy/sass/susy";
@import "~bourbon/app/assets/stylesheets/bourbon";
@import "~modularscale-sass/stylesheets/modular-scale";
@import "~breakpoint-sass/stylesheets/breakpoint";
@import "~reset-css/reset";

@import "./config/config";
@import "./utilities/_functions.scss";
@import "./utilities/_mixins.scss";
@import "./utilities/_variables.scss";
@import "./config/pattern-presets";

html {
    background-color: $color-grey-lightest;
    color: $color-grey-dark;
    font-family: $font-body, sans-serif;
    font-size: $base-font-size;
    line-height: 1;

    @include breakpoint($bp-beta) {
        font-size: $base-font-size * 1.1;
    }

    @include breakpoint($bp-zeta) {
        font-size: $base-font-size * 1.25;
    }

    @include breakpoint($bp-eta) {
        font-size: $base-font-size * 1.5;
    }

    @include breakpoint($bp-omega) {
        font-size: $base-font-size * 2;
    }
}

@import "./patterns/atoms/color-swatch/_color-swatch.scss";
@import "./patterns/atoms/content-area/_content-area.scss";
@import "./patterns/atoms/logo/_logo.scss";
@import "./patterns/molecules/button-group/_button-group.scss";
@import "./patterns/molecules/hidden-area/_hidden-area.scss";
@import "./patterns/molecules/menu/_menu.scss";
@import "./patterns/molecules/nav-grid/_nav-grid.scss";
@import "./patterns/organisms/code-block/_code-block.scss";
@import "./patterns/organisms/documentation/_documentation.scss";
@import "./patterns/organisms/header/_header.scss";
@import "./patterns/organisms/hero/_hero.scss";
@import "./patterns/organisms/iframe/_iframe.scss";
@import "./patterns/organisms/pattern/_pattern.scss";



